<template>
  <div class="PaperPublicationTotalBackground">
    <!--     <input
            type="text"
            class="PaperPublicationUserNumberInput"
            v-model="PaperPublicationUserNumber"
            placeholder="请输入学生学号"
          />
          <button  class="PaperPublicationSeekButton" @click="PaperPublicationSeekButtonClick">查询</button>
          <button  class="PaperPublicationEditButton" @click="PaperPublicationEditButtonClick">编辑</button>
       -->
    <div class="PaperPublicationTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 3%">姓名</th>
            <th style="width: 4%">论文名</th>
            <th style="width: 4%">doi</th>
            <th style="width: 4%">发表期刊名</th>
            <th style="width: 2%">卷号</th>
            <th style="width: 2%">期号</th>
            <th style="width: 3%">年分</th>
            <th style="width: 4%">发表日期</th>
            <th style="width: 4%">作者排名</th>
            <th style="width: 4%">指导老师</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(studentRecord, index) in PaperPublicationTable"
            :key="index"
          >
            <td>
              {{ studentRecord.paperList[0].studentName }}
            </td>
            <td>
              {{ studentRecord.paperList[0].paperName }}
            </td>
            <td>
              {{ studentRecord.paperList[0].doi }}
            </td>
            <td>
              {{ studentRecord.paperList[0].journalName }}
            </td>
            <td>
              {{ studentRecord.paperList[0].volumeNumber }}
            </td>
            <td>
              {{ studentRecord.paperList[0].issue }}
            </td>
            <td>
              {{ studentRecord.paperList[0].year }}
            </td>
            <td>
              {{ formatDate(studentRecord.paperList[0].yearOfPublication) }}
            </td>
            <td>
              {{ studentRecord.paperList[0].editorRanking }}
            </td>
            <td>
              {{ studentRecord.paperList[0].instructor }}
            </td>
            <!-- 查看详情 -->
            <td @click="ViewDetails(studentRecord)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="PaperPublicationPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>

    <div v-if="ifDetails" class="PaperPublicationDetailsBackground">
      <div class="PaperPublicationDetailsFormBackground">
        <span class="PaperPublicationDetailsFormTitle">学生论文详细信息</span>
        <form class="PaperPublicationDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>发表论文名 : {{ data.paperName }}</label>
            </div>
            <div class="label-row">
              <label>doi : {{ data.doi }}</label>
            </div>
            <div class="label-row">
              <label>发表期刊名 : {{ data.journalName }}</label>
            </div>
            <div class="label-row">
              <label>卷号 : {{ data.volumeNumber }}</label>
              <label>期号 : {{ data.issue }}</label>
              <label>年份 : {{ data.year }}</label>
            </div>
            <div class="label-row">
              <label>发表时间 : {{ data.yearOfPublication }}</label>
            </div>
            <div class="label-row">
              <label>作者排名 : {{ data.editorRanking }}</label>
              <label>指导老师 : {{ data.instructor }}</label>
            </div>
            <br />
          </div>
        </form>
        <button
          class="PaperPublicationDetailsFormCloseButton"
          @click="PaperPublicationDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      PaperPublicationTable: [],
      ifDetails: false,
      studentDetails: {},
      currentPage: 1,
      pageSize: 13,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    formatDate(date) {
      const d = new Date(date);
      return `${d.getFullYear()}-${(d.getMonth() + 1)
        .toString()
        .padStart(2, "0")}-${d.getDate().toString().padStart(2, "0")}`;
    },
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getPaperInformation",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.PaperPublicationTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },

    RenewLabel(studentRecord) {
      this.studentDetails = studentRecord.paperList;
    },
    PaperPublicationDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.PaperPublicationPagination {
  position: absolute;
  top: 95%;
}
.PaperPublicationUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.PaperPublicationTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.PaperPublicationTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/PaperPublicationForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
